import {Component, OnDestroy, OnInit} from '@angular/core';
import {ProductService} from '../product.service';
import {Product} from '../product.model';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {Subscription} from 'rxjs/Subscription';

@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.component.html',
  styleUrls: ['./product-detail.component.css']
})
export class ProductDetailComponent implements OnInit, OnDestroy {
  product: Product;
  id: number;
  productSubscription: Subscription;

  isLoading = false;

  constructor(private productService: ProductService,
              private route: ActivatedRoute,
              private router: Router) { }

  ngOnInit() {
    this.route.params.subscribe(
        (params: Params) => {
          this.id = +params['id'];
          this.loadProduct();
        }
    );
    this.productSubscription = this.productService.productLoaded.subscribe(
      (product: Product) => {
          this.product = product;
          this.isLoading = false;
          if (!this.product) {
              setTimeout(() => { this.onCloseModal() }, 3000);
          }
      }
    );
  }

  loadProduct() {
    this.product = this.productService.getProduct(this.id);
    if (!this.product) {
      this.isLoading = true;
      this.productService.loadProduct(this.id);
    }
  }

  onDelete() {
      this.productService.deleteProduct(this.id);
      this.onCloseModal();
  }

  onCloseModal() {
    this.router.navigate(['../'], {relativeTo: this.route, queryParamsHandling: 'merge'});
  }

  onOpenEdit() {
    this.router.navigate(['edit'], {relativeTo: this.route, queryParamsHandling: 'merge'});
  }

  ngOnDestroy() {
      this.productSubscription.unsubscribe();
  }

}



